html {
  justify-content: center;
  align-items: center;
  height: 100vh;
  display: flex;
}

body {
  margin: 0;
}

.stopwatch {
  border: 1vmin dotted #000;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  width: 80vmin;
  height: 80vmin;
  display: flex;
  position: relative;
}

.stopwatch__minute-hand {
  z-index: 2;
  transform-origin: 0 100%;
  background-color: #0700ff;
  width: 3vmin;
  height: 20vmin;
  animation: 3600s steps(60, end) infinite myanimation;
  position: absolute;
  bottom: 50%;
  left: 50%;
  transform: translate(-50%);
}

.stopwatch__second-hand {
  z-index: 1;
  transform-origin: 0 100%;
  background-color: #2c8000;
  width: 1.5vmin;
  height: 38vmin;
  animation: 60s linear infinite myanimation;
  position: absolute;
  bottom: 50%;
  left: 50%;
  transform: translate(-50%);
}

@keyframes myanimation {
  0% {
    transform: rotate(0)translate(-50%);
  }

  100% {
    transform: rotate(360deg)translate(-50%);
  }
}

.stopwatch__small-circle {
  z-index: 3;
  background-color: #f6a603;
  border-radius: 50%;
  width: 5vmin;
  height: 5vmin;
  position: absolute;
}

.stopwatch--speed-up .stopwatch__minute-hand {
  animation-duration: 600s;
}

.stopwatch--speed-up .stopwatch__second-hand {
  animation-duration: 10s;
}

